import React, {useEffect, useImperativeHandle} from 'react';
import {
  Textarea,
  TextareaChangePayload,
  TextareaInputPayload,
  TextareaStyleResize,
} from '@here/hds-textarea';

// eslint-disable-next-line no-new
new Textarea();

interface HDSTextareaProperties
  extends Omit<
    React.HTMLAttributes<HTMLTextAreaElement>,
    'onChange' | 'onBlur' | 'onFocus' | 'onKeyDown' | 'onKeyUp' | 'onInput'
  > {
  value?: string;
  id?: string;
  name?: string;
  placeholder?: string;
  label?: string;
  secondaryLabel?: string;
  disabled?: boolean;
  readonly?: boolean;
  required?: boolean;
  autofocus?: boolean;
  rows?: number;
  cols?: number;
  minlength?: number;
  maxlength?: number;
  charCounter?: boolean;
  resize?: TextareaStyleResize;
  error?: boolean;
  errorText?: string;
  autocomplete?: string;
  onChange?: (e: CustomEvent<TextareaChangePayload>) => void;
  onKeyDown?: (e: KeyboardEvent) => void;
  onKeyUp?: (e: KeyboardEvent) => void;
  onBlur?: (e: CustomEvent<Textarea>) => void;
  onFocus?: (e: CustomEvent<Textarea>) => void;
  onInput?: (e: CustomEvent<TextareaInputPayload>) => void;
}

const HDSTextarea = React.forwardRef((props: HDSTextareaProperties, ref) => {
  const {
    value,
    id,
    name,
    placeholder,
    label,
    secondaryLabel,
    disabled,
    readonly,
    required,
    autofocus,
    rows,
    cols,
    minlength,
    maxlength,
    charCounter,
    error,
    errorText,
    onChange,
    onKeyDown,
    onKeyUp,
    resize,
    onBlur,
    onFocus,
    onInput,
    className,
    autocomplete,
    ...rest
  } = props;
  const innerRef = React.useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleChange = (e: CustomEvent<TextareaChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e);
  };
  const _handleInput = (e: CustomEvent<TextareaInputPayload>) => {
    if (e.target !== innerRef.current) return;
    onInput && onInput(e);
  };
  const _handleBlur = (e: CustomEvent<Textarea>) => {
    if (e.target !== innerRef.current) return;
    onBlur && onBlur(e);
  };
  const _handleFocus = (e: CustomEvent<Textarea>) => {
    if (e.target !== innerRef.current) return;
    onFocus && onFocus(e);
  };
  const _handleKeyDown = (e: KeyboardEvent) => {
    if (e.target !== innerRef.current) return;
    onKeyDown && onKeyDown(e);
  };
  const _handleKeyUp = (e: KeyboardEvent) => {
    if (e.target !== innerRef.current) return;
    onKeyUp && onKeyUp(e);
  };

  useEffect(() => {
    if (onChange && innerRef.current) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => innerRef.current?.removeEventListener('change', _handleChange as EventListener);
  }, [onChange, innerRef]);

  useEffect(() => {
    if (onKeyDown && innerRef.current) {
      innerRef.current.addEventListener('keydown', _handleKeyDown as EventListener);
    }
    return () => innerRef.current?.removeEventListener('keydown', _handleKeyDown as EventListener);
  }, [onKeyDown, innerRef]);

  useEffect(() => {
    if (onKeyUp && innerRef.current) {
      innerRef.current.addEventListener('keyup', _handleKeyUp);
    }
    return () => innerRef.current?.removeEventListener('keyup', _handleKeyUp);
  }, [onKeyUp, innerRef]);

  useEffect(() => {
    if (onInput && innerRef.current) {
      innerRef.current.addEventListener('input', _handleInput as EventListener);
    }
    return () => innerRef.current?.removeEventListener('input', _handleInput as EventListener);
  }, [onInput, innerRef]);

  useEffect(() => {
    if (onBlur && innerRef.current) {
      innerRef.current.addEventListener('blur', _handleBlur as EventListener);
    }
    return () => innerRef.current?.removeEventListener('blur', _handleBlur as EventListener);
  }, [onBlur, innerRef]);

  useEffect(() => {
    if (onFocus && innerRef.current) {
      innerRef.current.addEventListener('focus', _handleFocus as EventListener);
    }
    return () => innerRef.current?.removeEventListener('focus', _handleFocus as EventListener);
  }, [onFocus, innerRef]);

  return (
    <hds-textarea
      value={value}
      id={id}
      name={name}
      placeholder={placeholder}
      label={label}
      {...(className && {class: className})}
      {...(secondaryLabel && {'secondary-label': secondaryLabel})}
      {...(disabled && {disabled: true})}
      {...(readonly && {readonly: true})}
      {...(autofocus && {autofocus: true})}
      {...(autocomplete && {autocomplete: autocomplete})}
      required={required}
      rows={rows}
      cols={cols}
      minlength={minlength}
      maxlength={maxlength}
      resize={resize}
      {...(charCounter && {'char-counter': charCounter})}
      {...(error && {error: true})}
      {...(errorText && {'error-text': errorText})}
      ref={innerRef}
      {...rest}
    ></hds-textarea>
  );
});

HDSTextarea.defaultProps = {
  resize: 'none',
};

export default HDSTextarea;
